html {
  line-height: 1.15;
  /* 1 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
  
}
body {
  background: #e0f3fa;
  background-image: url("/fondo.jpg");
 
}
body::before {
  content:'';
  width:100%;
  height: 100%;
  top:0;
  bottom:0;
  left:0;
  right:0;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.1);

}
/* Sections
     ========================================================================== */

/*** Remove the margin in all browsers.*/

body {
  margin: 0;
}

/**
   * Render the `main` element consistently in IE.
   */

main {
  display: block;
}

/**
   * Correct the font size and margin on `h1` elements within `section` and
   * `article` contexts in Chrome, Firefox, and Safari.
   */

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

/* Grouping content
     ========================================================================== */

/**
   * 1. Add the correct box sizing in Firefox.
   * 2. Show the overflow in Edge and IE.
   */

hr {
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  /* 1 */
  height: 0;
  /* 1 */
  overflow: visible;
  /* 2 */
}

/**
   * 1. Correct the inheritance and scaling of font size in all browsers.
   * 2. Correct the odd `em` font sizing in all browsers.
   */

pre {
  font-family: monospace, monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */
}

/* Text-level semantics
     ========================================================================== */

/**
   * Remove the gray background on active links in IE 10.
   */

a {
  background-color: transparent;
}

/**
   * 1. Remove the bottom border in Chrome 57-
   * 2. Add the correct text decoration in Chrome, Edge, IE, Opera, and Safari.
   */

abbr[title] {
  border-bottom: none;
  /* 1 */
  text-decoration: underline;
  /* 2 */
  -webkit-text-decoration: underline dotted;
          text-decoration: underline dotted;
  /* 2 */
}

/**
   * Add the correct font weight in Chrome, Edge, and Safari.
   */

b,
strong {
  font-weight: bolder;
}

/**
   * 1. Correct the inheritance and scaling of font size in all browsers.
   * 2. Correct the odd `em` font sizing in all browsers.
   */

code,
kbd,
samp {
  font-family: monospace, monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */
}

/**
   * Add the correct font size in all browsers.
   */

small {
  font-size: 80%;
}

/**
   * Prevent `sub` and `sup` elements from affecting the line height in
   * all browsers.
   */

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

/* Embedded content
     ========================================================================== */

/**
   * Remove the border on images inside links in IE 10.
   */

img {
  border-style: none;
}

/* Forms
     ========================================================================== */

/**
   * 1. Change the font styles in all browsers.
   * 2. Remove the margin in Firefox and Safari.
   */

button,
input,
optgroup,
select,
textarea {
  font-family: Georgia, Times, "Times New Roman", serif;
  /* 1 */
  font-size: 100%;
  /* 1 */
  line-height: 1.15;
  /* 1 */
  margin: 0;
  /* 2 */
}

button {
  text-align: center;
  margin:2px;
}

button.submit:hover {
  opacity: .85;
  cursor: pointer;
}

/**
   * Show the overflow in IE.
   * 1. Show the overflow in Edge.
   */

button,
input {
  /* 1 */
  overflow: visible;
}

/**
   * Remove the inheritance of text transform in Edge, Firefox, and IE.
   * 1. Remove the inheritance of text transform in Firefox.
   */

button,
select {
  /* 1 */
  text-transform: none;
}

/**
   * Correct the inability to style clickable types in iOS and Safari.
   */

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

/**
   * Remove the inner border and padding in Firefox.
   */

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

/**
   * Restore the focus styles unset by the previous rule.
   */

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

/**
   * Correct the padding in Firefox.
   */

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

/**
   * 1. Correct the text wrapping in Edge and IE.
   * 2. Correct the color inheritance from `fieldset` elements in IE.
   * 3. Remove the padding so developers are not caught out when they zero out
   *    `fieldset` elements in all browsers.
   */

legend {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  /* 1 */
  color: inherit;
  /* 2 */
  display: table;
  /* 1 */
  max-width: 100%;
  /* 1 */
  padding: 0;
  /* 3 */
  white-space: normal;
  /* 1 */
}

/**
   * Add the correct vertical alignment in Chrome, Firefox, and Opera.
   */

progress {
  vertical-align: baseline;
}

/*Remove the default vertical scrollbar in IE 10+.*/

textarea {
  overflow: auto;
}

/** 1. Add the correct box sizing in IE 10.
  * 2. Remove the padding in IE 10.*/

[type="checkbox"],
[type="radio"] {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  /* 1 */
  padding: 0;
  /* 2 */
}

/* Correct the cursor style of increment and decrement buttons in Chrome. */

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

/* * 1. Correct the odd appearance in Chrome and Safari.
   * 2. Correct the outline style in Safari.
   */

[type="search"] {
  -webkit-appearance: textfield;
  /* 1 */
  outline-offset: -2px;
  /* 2 */
}

/* Remove the inner padding in Chrome and Safari on macOS.*/

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
   * 1. Correct the inability to style clickable types in iOS and Safari.
   * 2. Change font properties to `inherit` in Safari.
   */

::-webkit-file-upload-button {
  -webkit-appearance: button;
  /* 1 */
  font: inherit;
  /* 2 */
}

/* Interactive
     ========================================================================== */

/*
   * Add the correct display in Edge, IE 10+, and Firefox.
   */

details {
  display: block;
}

/*
   * Add the correct display in all browsers.
   */

summary {
  display: list-item;
}

/* Misc
     ========================================================================== */

/*** Add the correct display in IE 10+.*/

template {
  display: none;
}

/*** Add the correct display in IE 10.*/

[hidden] {
  display: none;
}

/*====================================Targetas====================================== */

.envolturaLogin {
  position: absolute;
  /*left y top al 50% para que quede centrado en la pantalla*/
  left: 50%;
  top: 50%;
  margin-left: -160px;
  margin-top: -200px;
  width: 300px;
}

.envoltura {
  position: absolute;
  /*left y top al 50% para que quede centrado en la pantalla*/
  left: 50%;
  top: 50%;
  margin-left: -160px;
  margin-top: -200px;
  width: 330px;
}

.envolturaGrande {
  position: absolute;
  left: 45%;
  top: 40%;
  margin-left: -160px;
  margin-top: -200px;
  width: 500px;
}

.contenedor {
  background-color: rgb(157, 202, 196);
  /*Margen de sombra alrededor del contenedor 8px negro*/
  -webkit-box-shadow: 3px 3px 10px 5px rgba(0, 0, 0, .8);
          box-shadow: 3px 3px 10px 5px rgba(0, 0, 0, .8);
  /*Las 3 líneas siguientes, sirven para el borde redondeado
       * pero para diferentes navegadores*/
  border-radius: 10px;
}

.cabecera {
  /*linea azul que separa logo*/
  border-bottom: 5px solid #333;
  padding-top: 3px;
  /*color:#FFF;*/
  height: 50px;
  line-height: 25px;
  text-align: center;
}

.cuerpo {
  background: #ececec;
  border: solid #ccc;
  /*aumentando el 2px 'aparece' un borde*/
  border-width: 2px 0;
  padding: 15px 35px;
}

.footer {
  border-top: 5px solid #333;
  color: #fff;
  font-size: 12px;
  height: 30px;
  line-height: 15px;
  text-align: center;
}

/*====================================Formularios====================================== */
label {
  color: #666;
  font-weight: bold
}

input {
  -webkit-box-shadow: 0px 0px 3px #ccc, 0 10px 15px #eee inset;
          box-shadow: 0px 0px 3px #ccc, 0 10px 15px #eee inset;
  border-radius: 2px;
  padding-right: 30px;
  -webkit-transition: padding .25s;
  -o-transition: padding .25s;
  transition: padding .25s;
}

button.submit {
  margin-left: 140px;
}

button.submit:hover {
  opacity: .85;
  cursor: pointer;
}

form,
p {
  margin: 0;
}


